var render = function render(){var _vm=this,_c=_vm._self._c;return _c('div',{staticClass:"hello"},[_c('div',{staticClass:"ui segment container"},[_c('h2',[_vm._v("1.5. 暗血石")]),_c('h3',[_vm._v("1.5.1. 『海之擁』")]),_c('p',[_vm._v("洛南紀五十九年十月十九日，集漁鎮")]),_c('p',[_vm._v("流星雨剛剛結束，人們躲回了家中。整個鎮上靜悄悄的，彷彿被施了魔咒一般。")]),_c('p',[_vm._v("全鎮唯一還亮著燈的房子裡，傳出陣陣喧嘩聲。那聲音在寂靜的巷弄間穿梭來回，飄進村口逐漸駛近的馬車裡。")]),_c('p',[_vm._v("「終於到了，」米庫斯高興地說道，「這兒就是集漁鎮啦，我半年前還來過這兒。這裡的旅店『海之擁』也算小有名氣，老闆卡穆里是個好客的人啦。」")]),_c('p',[_vm._v("「真不錯，可是街道上竟然一遍死寂...」安琪道，「一定是流星雨的關係。」")]),_c('p',[_vm._v("「我想也是，」米庫斯道，「星斗真的隕落了，這對人們一定打擊不小吧。」")]),_c('p',[_vm._v("「你難道不受影響？」安琪問道，「我們主神的星座都落了一顆星欸。這一定有什麼啟示。」")]),_c('p',[_vm._v("「如果要問我啊，」米庫斯笑道，「一定是神明在天上待得不耐煩，下來透透氣啦。」")]),_c('p',[_vm._v("「別亂講。」")]),_c('p',[_vm._v("「好啦，好啦，」米庫斯道，「先下車再說，咱們到了。」")]),_c('p',[_vm._v("兩人下車，向車夫道了謝。米庫斯今日的車費付給他，一共十枚碎金。")]),_c('p',[_vm._v("車夫將馬匹連著車子牽往馬廄，兩人走進了掛著亮藍色招牌的旅店「海之擁」。")]),_c('p',[_vm._v("寬敞的大廳上，十幾個人席地而坐，圍著火堆取暖，一邊談天說地。")]),_c('p',[_vm._v("兩名店小二忙進忙出，端上一壺一壼的酒，以及切好的牛肉。")]),_c('p',[_vm._v("壯碩的老闆卡穆里站在大廳右首的吧台後，一看到兩人進來，立刻熱誠地說道：「新來的客人啊，你們一定趕了不少路吧。來來來，先到火堆邊坐坐，烤烤火。這種天氣啊，真教人不想待在外面呢。」")]),_c('p',[_vm._v("他看起來約四十來歲，粗壯的雙臂和臉孔完全沒有掩蓋他那熱誠和善的笑容。歲月在他的眼中留下了智慧的光芒，而不懈地鍛鍊讓他維持了年輕的體力。")]),_c('p',[_vm._v("「你不記得我啦，卡穆里？」米庫斯說著摘下兜帽。")]),_c('p',[_vm._v("「對了，我想起來了，你是那個...」卡穆里笑道，「米庫斯！對了，鎖匠米庫斯！各位，米庫斯回來了！」")]),_c('p',[_vm._v("一聽到米庫斯的名字，店裡的人紛紛轉頭張望。")]),_c('p',[_vm._v("鎖匠米庫斯？安琪心道，什麼時候他從修士變成鎖匠了？他出外浪遊的這幾年，到底學了些什麼本事呢？")]),_c('p',[_vm._v("「我來跟各位介紹，」米庫斯道，「這是我的表妹安琪啦。她是潮汐神的修士，也是很高明的醫者呢。」")]),_c('p',[_vm._v("「很高興認識各位。」安琪禮貌地說道。")]),_c('p',[_vm._v("「唉呀呀，真是稀客稀客，」卡穆里道，「難得見到在外冒險的小姐。好啦，今天兩位的食宿，就算我請客，大家交個朋友！」")]),_c('p',[_vm._v("「這怎麼好意思呢？」安琪道。")]),_c('p',[_vm._v("米庫斯暗地裡給了她一小記肘捶。由於米庫斯個子小，安琪則比常人稍高，這記肘捶正好撞到腰眼上。安琪不願失禮，只好忍痛，假作不知。")]),_c('p',[_vm._v("「不要緊，不要緊，」卡穆里笑道，「你們就先坐吧。來來來，那邊的朋友，麻煩讓個位兒。」說著將兩人帶到火堆北首，靠窗的位置坐下。")]),_c('h3',[_vm._v("1.5.2. 鬼故事")]),_c('p',[_vm._v("「剛剛講到哪兒啦？」一個面帶醉意的大漢問道。")]),_c('p',[_vm._v("「鬼船，」一個留著兩撇鼠鬚的瘦小漢子說道，「不過我想我應該重頭開始，不然他們跟不上。」")]),_c('p',[_vm._v("「那就有勞啦。」米庫斯道，同時擺出一個輕鬆的姿式。")]),_c('p',[_vm._v("「這故事發生在東南方的海岸，是一個漁夫親看眼到的。」瘦子道，「三天前的夜裡，當漁夫正在岸邊採海菜時，注意到一陣濃霧，霧氣中有一個若隱若顯的白影，竟有帆船的輪廓。」")]),_c('p',[_vm._v("「帆船？」米庫斯笑道，「不會是鬼船吧？」")]),_c('p',[_vm._v("「就是鬼船，正是鬼船！」瘦子道，「在瀰漫的霧中，塔加不知眼前的景像是真是幻。他趕緊伏倒，躲到岩石後面。」")]),_c('p',[_vm._v("他續道：「接著鬼船愈來愈近，周圍的空氣也愈來愈冷。陣陣陰風自海上吹來，彷彿鬼哭從船上飄臨。『嗚...啊...』大概就像這樣，『嗚...哦...啊...』」")]),_c('p',[_vm._v("說故事的人用喉頭裝出低沉的喊聲，像鬼魅的掙扎叫喊，讓溫暖的火爐邊平添一般寒意。")]),_c('p',[_vm._v("安琪從小聽慣了鬼故事，絲毫不覺可怕，反而聽得起了興。")]),_c('p',[_vm._v("不過，既然星斗都會隕落，這鬼船的故事說不定也是真的，那就不再有趣了。")]),_c('p',[_vm._v("那人繼續說道：「塔加嚇得發抖，完全不敢動彈。傳說啊，只要讓鬼船的船長看見了，就會被詛咒，被帶走，變成鬼船上的一員。")]),_c('p',[_vm._v("「他靜靜地看著鬼船靠岸，也沒有水鬼下來繫纜，神奇地停住了。")]),_c('p',[_vm._v("「霧氣仍然很重，而陣陣寒風已冷到讓塔加幾乎抵受不住。他顫抖著，又不敢離開藏身之處，只好一直等待，希望一切早點結束。")]),_c('p',[_vm._v("「這時，從鬼船上走下一個披著紅袍的身影，面目瞧不清楚。接著，又下來了一個披黑斗蓬的和一個赤裸上身的人，也都看不清面孔。")]),_c('p',[_vm._v("「他們用奇怪的語言交談，然後各自散開，往不同的方向走去。披著紅斗蓬的人，竟筆直地走向塔加的藏身之處！")]),_c('p',[_vm._v("「塔加看著那人一步一步地走近。一股恐懼感將他攏罩，他幾乎快窒息了...」")]),_c('p',[_vm._v("那人說到這裡，停了一下，喝了一小口酒，拿了塊肉片，邊嚼邊繼續道：「嗯。他一動也不動，只能不停地向災禍之神祈禱。紅袍人愈走愈近，眼看就要到他的身邊了！")]),_c('p',[_vm._v("「說也奇怪，或許是霧太濃，紅袍怪客竟筆直地從離他一步之遙的沙地走過，完全沒注意到塔加的存在。")]),_c('p',[_vm._v("「那人斗蓬半遮的臉上，有著一道很長的疤痕，掠過他沒有瞳孔的左眼。臉上的神情，像是剛從地獄冒出來的，簡直比鬼還可怕。")]),_c('p',[_vm._v("「塔加等著，直到那人走遠，鬼船也不知何時消失得無影無蹤。濃霧漸漸散開，星月重現光明，寒氣也不再刺骨。")]),_c('p',[_vm._v("「剛才的一切，就像一場夢境，褪去得絲毫不留痕跡。塔加慢慢起身，發現沙灘上真的有幾排腳印。這是真的！」")]),_c('p',[_vm._v("「你是說，」一個方臉漢子問道，「三個怪人在大霧中乘著鬼船上了岸，之後就不見蹤影？真不錯，可惜沒有新意，老套的鬼故事啦。」")]),_c('p',[_vm._v("人群中爆出一陣哄笑。")]),_c('p',[_vm._v("說故事的人紅著臉道：「你們可別笑，這是真人真事。我就是從塔加那兒聽來的。」")]),_c('p',[_vm._v("「我才不信呢，」有人道，「塔加在哪兒？」")]),_c('p',[_vm._v("「就在樓上房裡睡著呢，」那人道，「自從事情發生以後，他夜夜失眠，總是在夢中驚醒，口中喃喃自語：『災難近了...末日近了…近了...從海的那一頭...』")]),_c('p',[_vm._v("「有時他會說：『他們來了...不要...戰船...一百艘...兩百艘...他們燒了我們的村子...殺了我們的人...』」")]),_c('p',[_vm._v("他摹仿塔加魂不附體，斷斷續續的顫音，在場的人都不禁打了個寒顫。")]),_c('p',[_vm._v("「惡夢糾纏著他，」那人續道，「於是他逃離家鄉，為了尋求一個安穩的所在，來治癒內心的傷。我是他的朋友，名叫梅卡，也是一名漁夫。」")]),_c('p',[_vm._v("「我想我知道安全的地方。」安琪道。")]),_c('p',[_vm._v("所有人的目光轉向她。")]),_c('p',[_vm._v("「神廟，」安琪道，「只要不是供奉邪神的就好。我知道這附近就有潮汐神的神廟。」")]),_c('p',[_vm._v("「那好，」梅卡道，「明早我就帶他去打探，應該不會太難找吧？」")]),_c('p',[_vm._v("「我想是不會，」安琪道，「不過我建議你現在先把那位塔加先生叫醒，我可以請潮汐神幫他祛除心中