風，」柔和的聲音說道，「你們終於來了。」")]),_c('p',[_vm._v("「安…安琪？」彼德道，「怎麼可…妳還活著？」")]),_c('p',[_vm._v("「安琪？真的是妳？」瓦林娜道，「可是在古墓…」")]),_c('p',[_vm._v("「其實事情不難懂。真的。」聲音說道，「我很高興可以跟你們重會。」")]),_c('p',[_vm._v("「可是在古墓…」")]),_c('p',[_vm._v("「轉生的大法師佔了我的驅體，但我的靈魂沒有消失。就像大法師的靈魂沒有消失一樣的。」安琪道，現在大家都確定那是安琪了。「現在我雖然沒有肉體，但是在接天橋上，我不需要肉體，也能好好活著。」")]),_c('p',[_vm._v("「像神或是鬼那樣？」彼德哭道，「我想看看妳，抱抱妳。」")]),_c('p',[_vm._v("「別急，我所愛著的夥伴。」安琪道，「你們要登上橋，也會暫離肉體。在接天橋上，有很多冰封的肉體，在靈魂轉生後，被運送過來。當我要再次回到人世時，不怕沒有身體。」")]),_c('p',[_vm._v("「被運送過來？」米庫斯奇道，「誰運送它們？」")]),_c('p',[_vm._v("「很多事情一時三刻說不清。敵人快來了，先上橋吧。你們的肉體來不及保護了，要趕在宿命星使紫焰來到前，完成天橋上的任務。」")]),_c('p',[_vm._v("「任務？封聖是...」")]),_c('p',[_vm._v("「封聖是騙人的事。」安琪道，「上天就是封神，沒上天就是凡人，沒有所謂的封聖。我們真正的任務，我在這裡研究很久了...只有一個辦法：建一張神秘的帷幕，把天遮起來，讓神力不再能透過星光，影響凡間。我在等你們。等到任務完成，我將回到永動的輪子，進入不息的風。」")]),_c('p',[_vm._v("彼德哭了。夥伴們也都流淚。")]),_c('p',[_vm._v("這時，銀光一閃，英雄們來到了光的世界。")]),_c('p',[_vm._v("這裡只有光，和一條一條銀色的絲線，連接著他們和他們的肉體，還有他們彼此。")]),_c('p',[_vm._v("這裡只有意念，沒有語言，他們合作的意念，讓銀色的絲線擺動、流動。")]),_c('p',[_vm._v("「要如何織天幕呢？」「元素在這裡都不管用了」「銀絲？」「等等，有東西來了」")]),_c('h3',[_vm._v("5.7.4. 真相與天幕")]),_c('p',[_vm._v("一團黑暗，沒有形狀，沒有面孔，靠近來過來。")]),_c('p',[_vm._v("「牠是『食靈妖』，專門吃人的意念。」一個新的聲音說道。")]),_c('p',[_vm._v("「誰來了？」「怎麼會？」「先解決食靈妖再...」")]),_c('p',[_vm._v("「意念被吃....」「這樣怎...」")]),_c('p',[_vm._v("「不...」")]),_c('p',[_vm._v("「停下！不要思考！這是唯一的方法！」新的聲音說道。")]),_c('p',[_vm._v("「…」")]),_c('p',[_vm._v("「...」")]),_c('p',[_vm._v("「注意聽！這是我的聲音！我是結界大師吉魯德！」新的聲音說道，「我瞭解環境，可以在星界施法！食靈妖，退去！」")]),_c('p',[_vm._v("「好險。」「差一點就被吃了。」「為什麼這裡有食靈妖？」")]),_c('p',[_vm._v("「是九主神佈下的。」")]),_c('p',[_vm._v("「我是安琪。在古墓我看到你們在封聖前遇襲：不知從何而來的火球，驟然在人群中爆裂。無中生有的閃電，指著愛森的衛兵，還有冥想中的大法師，四射而出。」")]),_c('p',[_vm._v("「痛苦的回憶。我是吉魯德。在那之前我們已經短暫上去過了。等著我們的是食靈妖。」")]),_c('p',[_vm._v("「不是天梯？」")]),_c('p',[_vm._v("「天梯從來就在那裡，誰都可以上去。把後來上去的精靈打落凡間，變成寶石的，不是莫可名者，而是九主神。我是吉魯德。這是我真確瞭解的事。星光是自然的，上來的精靈給自己發光，假扮成星星。其實隕星之夜隕落的，一開始就不是真的星星。」")]),_c('p',[_vm._v("「什麼？傳說中是真神把過多的星辰打落凡間。我是瓦林娜。」")]),_c('p',[_vm._v("「傳說只是傳說。」")]),_c('p',[_vm._v("「我是米庫斯，我為曾經崇拜浪濤神感到可恥。」")]),_c('p',[_vm._v("一道閃電打向他們。")]),_c('p',[_vm._v("「別忘了這裡這星界！我是吉魯德！閃電聽我令，速反射！呼！雖然張了結界，但是還是不要輕易提到他們的稱號或名字比較安全。事實上，在結界外，我們已被食靈妖包圍了。」")]),_c('p',[_vm._v("黑暗來自四面八方，沒有面孔、沒有聲音。")]),_c('p',[_vm._v("「要怎麼張天幕？我是烏風，我要在此跟安琪道歉。是我害的。這比張天幕重要。」")]),_c('p',[_vm._v("「沒有關係，我現在是在光中，我們都是。我現在是在愛中，我們都是。我是安琪，我要集合我們的力量，以及結界大師的支援，張開遮天帷幕。」")]),_c('p',[_vm._v("「我是吉魯德，在星界的帷幕不會遮蔽星光，只會遮蔽往下和往上傳送的法力。換言之，修士將不能向主神禱告求得神蹟。下凡的星使也不再擁有無盡的法力。」")]),_c('p',[_vm._v("「我是彼德。那誰來驅邪和治傷呢？」")]),_c('p',[_vm._v("「以日光驅邪，以醫藥治傷。我是烏風。」")]),_c('p',[_vm._v("「誰來破除詛咒、抵擋暗血石和焚靈火呢？」")]),_c('p',[_vm._v("「『往生』聖鎚的繼承人，修士西薇莉。我是瓦林娜，我和烏風在逃亡的旅程中遇到過她，還帶著雷和嵐兩個聰明的小孩。嵐才八歲，已經能施簡單的法術了。雷和烏風學了很多。」")]),_c('p',[_vm._v("「帶著小孩的冒險者，能成大事？我是米庫斯。」")]),_c('p',[_vm._v("「所以我們要去幫忙啊。我是彼德。這裡完工之後，我們就去找這位修士西薇莉吧。」")]),_c('p',[_vm._v("「織天幕不易，但要封印幽域之門，更是困難重重。我是吉魯德。但是，先別擔心未來，我們專心織天幕吧。安琪妳來祝禱，我沈默施法。」")]),_c('p',[_vm._v("「我是安琪。我在此宣佈封印星界，讓天凡二界的法力，彼此不能相通！」")]),_c('p',[_vm._v("「我是安琪。彼德集中四大元素的力量，烏風集中清醒的神識，瓦林娜召集不息的風，米庫斯旋轉永動的輪子。只要一個變化，就能產生遮天大能。」")]),_c('p',[_vm._v("「勇者烏風，帶著勇氣；恆者瓦林娜，帶著希望；強者彼德，帶著力量；智者米庫斯，帶著覺察；我是仁者安琪，我帶著真心。」")]),_c('p',[_vm._v("「勇氣，希望，力量，覺察，真心。」")]),_c('p',[_vm._v("「合而為一。」")]),_c('h2',[_vm._v("#尾聲")]),_c('p',[_vm._v("傳說天幕就這樣張起。傳說自由將支配遮蔽。傳說凡人就這樣封聖。封聖之後仍是凡驅。")]),_c('p',[_vm._v("傳說天橋五聖，在兩位古老的大法師的協助下，完成了不朽的使命。")]),_c('p',[_vm._v("勇者烏風，流著幽暗的血液，歷經而殺戮和身不由己，在惡夢與饑寒中渡過，現在終於找到了夥伴，和愛與真心。")]),_c('p',[_vm._v("恆者瓦林娜，在不息的風下成長，飄泊四處，偽裝逃避，像風箏一樣，現在終於找到了夥伴，和愛與穩定。")]),_c('p',[_vm._v("強者彼德，在大地的祝福中成長，遠離人間、不經世事，曾經天真受欺，現在終於找到了夥伴，和愛與真知。")]),_c('p',[_vm._v("智者米庫斯，在波浪的翻騰中成長，遭受誤解，備受輕視，現在終於找到了夥伴，和愛與尊嚴。")]),_c('p',[_vm._v("勇氣，希望，力量，覺察，真心。合而為一。")]),_c('p',[_vm._v("傳說他們在回程的路上，沒有再遇到宿命星使紫焰，他們改變了自己的命運。")]),_c('p',[_vm._v("天幕張起了，九大星使也自此隱遁。有人說他們回到了森林，有人說在天幕張起前再次飛升天界。")]),_c('p',[_vm._v("北方秩序星使的王國和米爾王和談而後合併；東北的平衡星使隱遁後，許多遊俠仍致力於守護平衡；東方海盜和商人，在沒有混亂星使的統一之下，又回到各自為政的局面。")]),_c('p',[_vm._v("在王都附近，審判星使走了，但幽域之門已開，恐怖精怪、闇精靈與活屍，愈來愈多，修士失去了神力，難以驅邪，使得貿易路線也被中斷，王都和落雲城岌岌可危。值得慶幸的是隨著災禍星使的離去，奪走沃土七成的人命的瘟疫和飛蝗終於告終了。莊稼又再度生長。")]),_c('p',[_vm._v("天幕張起了，幽域成為新興的主要威脅。大法師復活，看似一件好事，但是，只有冥王、吸血公爵薩瑪蘭和幽域的闇精靈知道，曾經幫助天橋五聖的愛森大法師，終有一天，將會成為他們的敵人。（The End）")]),_c('div',{staticClass:"ui buttons"},[_c('router-link',{staticClass