不是普通人所能掌握的。連法師很難控制它的力量。」")]),_c('p',[_vm._v("一陣短暫的沉默之後，烏風解釋道：「對方是一群很早就發現古代魔法的人，他們的首領曾經和菲妮安等人共事。」")]),_c('p',[_vm._v("「曾經？」")]),_c('p',[_vm._v("「他們當時都是考古學者，後來發現魔法後，因意見不合而分道揚鑣。妳的另一位師父，舞空者耐文的一雙腳，就是被他施風切術截斷的。」")]),_c('p',[_vm._v("「那...他的名字叫什麼？」")]),_c('p',[_vm._v("「取走神球的法師，人稱冰風大師寇維爾，十分高強，」烏風道，「不過失去了同伴的協助，他的進益應該比妳的師父們要慢一些。」")]),_c('p',[_vm._v("「冰風大師寇維爾，寇維爾...嗯，應該是吧，」瓦林娜道，「畢竟深奧的法術很難獨立鑽研。不過，如果那個寇維爾有把握控制神球，他的法力肯定遠在我之上。」")]),_c('p',[_vm._v("「所以妳需要我的協助。」烏風道。")]),_c('p',[_vm._v("「的確，你說得對，」瓦林娜道，「但是為什麼我們不能等芬斯特藍和歐利夫呢？他們會成為一大助力的。」")]),_c('p',[_vm._v("「一大助力？以他們的實力，遇到冰風大師只會白白送死，」烏風道，「妳不會希望讓芬斯特藍去送死吧？」")]),_c('p',[_vm._v("瓦林娜深入了一陣沉默。若不是她已隱形，烏風便會看到她羞澀的表情。言者無心，聽者有意，瓦林娜對芬斯特藍已有了一份感情，只是她自己也不清楚，這是否只是單純的夥伴情誼，還是...")]),_c('p',[_vm._v("「況且，」烏風勸道，「我們可以奪了神球再去找他們會合。」")]),_c('p',[_vm._v("「好吧，就這麼辦，」瓦林娜想了一下，說道，「但你要多告訴我一點關於你的事，否則我會覺得你很瞭解我的背景，我卻對你一無所知，不大公平。」")]),_c('p',[_vm._v("「如果我跟妳說了，妳會後悔要我講的。」烏風道。他握著瓦林娜的手，往守衛站的右方走去，朝向視線之外的廢棄考古場。")]),_c('p',[_vm._v("兩個隱形的人，在平原上走著。")]),_c('p',[_vm._v("沒有人看得見他們的身影，但這趟旅程將會影響所有的人。")]),_c('p',[_vm._v("「好吧，好吧，故作神秘，哼。」瓦林娜慍道，「至少你可以講點遺產和歷史之類的事吧。以前師父都不大提。」")]),_c('p',[_vm._v("「有些事太黑暗了，還是不要知道比較好。」烏風道。")]),_c('p',[_vm._v("「但是，愈去逃避黑暗，它愈會增長啊。還不如去面對它。」瓦林娜急道，「唯有探索才能對抗對未知的恐懼啊。」")]),_c('p',[_vm._v("「如果黑暗像是個無底洞，愈去探索，愈會陷入其中，那便如何？」")]),_c('p',[_vm._v("「將它照亮吧。」瓦林娜笑道。")]),_c('p',[_vm._v("霎時間，烏風覺得一道電流通過了全身，將他震攝住。他受了太多的折磨，卻在瓦林娜的一句話中得到了救贖。至少，他看到了希望之光。")]),_c('p',[_vm._v("「將它照亮吧。」瓦林娜是這麼說的。")]),_c('p',[_vm._v("一個黑暗的子民，如何能夠發亮？他心道，或許，這便是我一直在尋找的，不刺眼的光。")]),_c('h3',[_vm._v("2.2.4. 烏風的童年")]),_c('p',[_vm._v("洛南紀五十二年六月二十一日，王都諾薩里亞貧民窟")]),_c('p',[_vm._v("「把他抓住！把他抓住！」一群十來歲的混混，圍著一個小小的身影，叫囂著。")]),_c('p',[_vm._v("那個小男孩約是十二歲上下，載著黑色頭套，被幾個大孩子逼到牆角，背牆而立，拳頭握得緊緊的。他身上穿著破爛的衣服，露出灰色的皮膚。")]),_c('p',[_vm._v("「把頭套拿下來嘛！」一個小混混惡毒地笑道，「我們又不會怎樣！只不過想看一看你的臉嘛！」")]),_c('p',[_vm._v("「對啊，有什麼好神秘兮兮的！」另一人搭腔道，「別以為你是灰色的就了不起，小灰！」")]),_c('p',[_vm._v("「小灰來，小灰來！」有人把他當狗耍。")]),_c('p',[_vm._v("「喂，小灰，」帶頭的混混說道，「你撐得也夠久啦。現在投降，沒人會說你孬。不然，我們可要動手囉。」")]),_c('p',[_vm._v("小男孩仍舊不肯取下頭套和蒙面布，只是憤怒地顫抖，發出野獸一般的低吼。")]),_c('p',[_vm._v("混混們一擁而上，亂拳擊向小男孩。他雖然身手不凡，但人小力氣也小，加上人數懸殊，沒多久便被打倒在地，牢牢按住。")]),_c('p',[_vm._v("帶頭的少年用力扯下男孩的頭套和蒙面布。")]),_c('p',[_vm._v("男孩緊閉眼睛，表情十分痛苦，似乎正午的陽光對他是一種傷害。")]),_c('p',[_vm._v("「小灰怕光！小灰怕光！」有人笑道。")]),_c('p',[_vm._v("「難怪是灰色的，原來流著惡魔的血啊！」另一人道。")]),_c('p',[_vm._v("「只有冥府來的怪物才怕光。」又一人說道。")]),_c('p',[_vm._v("「怕什麼，還不是給我們制住了！」最先開口的那人笑道。")]),_c('p',[_vm._v("「拿火把來！」帶頭的命令道。")]),_c('p',[_vm._v("不久，一個點著的火把便被拿了過來。帶頭的少年將火把拿近男孩的臉。由於被緊緊地按住，他根本無法閃避。灼熱的光芒像利劍一樣刺入了他的雙眼，就算緊閉著也沒有比較好過。")]),_c('p',[_vm._v("他就這樣被折磨著，直到帶頭的發現已經燒傷了他的面頰，才嚇得一哄而散。他從頭到尾沒有吭過一聲。")]),_c('p',[_vm._v("男孩花了一個月才恢復眼力。被燒傷的左頰，留下一大塊無法復原的疤。")]),_c('p',[_vm._v("不過，有著超強記憶力的他，將仇人的面孔一個不漏地記下，並迅速地復仇。")]),_c('p',[_vm._v("當天在場的那些人，一年之內都被暗殺了。殺手是一個十二歲的無名男孩。")]),_c('p',[_vm._v("月影知道了這事後，很快地找他入夥。")]),_c('p',[_vm._v("短短數年內，他成了冷血、高強、可靠、沉默的殺手。許多人看好他未來能統領全月影的殺手，當上望部的總堂主。")]),_c('p',[_vm._v("他是烏風。")]),_c('div',{staticClass:"ui buttons"},[_c('router-link',{staticClass:"ui orange huge button",attrs:{"to":"/b1"}},[_vm._v("2.1節")]),_c('router-link',{staticClass:"ui green huge button",attrs:{"to":"/b3"}},[_vm._v("2.3節")])],1)])])
