)]),_c('p',[_vm._v("「這很邪惡。」安琪道，「我們自己不想死，又怎能殺害無辜的人？」")]),_c('p',[_vm._v("一陣沉默。")]),_c('h3',[_vm._v("5.5.2. 是非善惡")]),_c('p',[_vm._v("喚醒大法師有轉機，但是這個轉機卻是…犧牲四個無辜的人。")]),_c('p',[_vm._v("「很難抉擇，是嗎？」一個冷冷的聲音自空氣中發出。")]),_c('p',[_vm._v("「烏風！」瓦林娜驚叫。")]),_c('p',[_vm._v("「沒錯，」烏風道，「我和米庫斯都來了。」")]),_c('p',[_vm._v("他關閉隱匿之球的力量，和米庫斯從空氣中現身。")]),_c('p',[_vm._v("「我們都聽到了，」米庫斯道，「我們到的時候，妳們剛開始在唸結界大師的指示。」")]),_c('p',[_vm._v("「我們必須喚醒大法師。」烏風冷冷地說道。蒙面布蓋著他的表情，遮住了掙扎和不忍的情緒。")]),_c('p',[_vm._v("「但是…」瓦林娜感到困難。")]),_c('p',[_vm._v("「彼德，你怎麼說？」安琪問。")]),_c('p',[_vm._v("「殺無辜的人當然是壞事，」彼德道，「但是，有千千萬萬的人會因此得救，免於戰爭、饑荒和恐懼。如果我們現在放棄，那就什麼都沒有了。不過…說實在我下不了手。」")]),_c('p',[_vm._v("「我們可以去找死有餘辜的人。」米庫斯道。")]),_c('p',[_vm._v("「在古墓附近？」彼德道，「別說死有餘辜的人，閃金村遷走了，你要找到一個人都很難。」")]),_c('p',[_vm._v("「既然這樣，我們就別管這個古墓了吧。」安琪道。她很不希望看到同伴犯下殺害無辜的惡行。")]),_c('p',[_vm._v("「我不是很想說，不過，」瓦林娜道，「我知道這附近有還未荒廢的考古營地。」")]),_c('p',[_vm._v("「問老西爾就知道，」米庫斯道，「他成為活屍之前，肯定也是考古學家。」")]),_c('p',[_vm._v("「我一個人上去就夠了。」烏風道，「我們可以一具一具屍體搬。」")]),_c('p',[_vm._v("「瑪法達在上，我們不能直接離開古墓嗎？」安琪怒道，「殘殺無辜是邪惡的行為。不論有什麼高尚的動機，謀殺就是謀殺。我是醫者，不能容許這樣的事。」")]),_c('p',[_vm._v("瓦林娜突然想起鐘墨芝石的話：「我只看到，你們在古墓會遇到很大的危險，還會面對一次很大的爭執。可能有人會送命。」")]),_c('p',[_vm._v("她對同伴說：「好了，不要啟爭端。我們都是同伴，不是嗎？」")]),_c('p',[_vm._v("「依妳看，事情怎麼辦？」米庫斯道，「我表妹硬是不肯，彼德也下不了手。」")]),_c('p',[_vm._v("瓦林娜還未接口，烏風卻搶先說話。")]),_c('p',[_vm._v("「小不忍，亂大謀。」烏風冷冷地說道，「我從小就是殺手，這些罪惡就由我來擔吧。」")]),_c('p',[_vm._v("說著，他啟動隱匿之球的法力，消失了。")]),_c('p',[_vm._v("「不！現身，烏風！」瓦林娜道，「還沒商量的事，不要衝動！」")]),_c('p',[_vm._v("烏風沒有回應。")]),_c('p',[_vm._v("「他去殺人了，」安琪絕望地說道，「他用神球逃走了，現在我還能做什麼呢？」")]),_c('p',[_vm._v("「等待吧，」米庫斯安慰道，「妳盡力了。」")]),_c('p',[_vm._v("安琪踱步著，又踱步著。她閉上眼，開始祈禱。")]),_c('p',[_vm._v("時間一分一秒地過去，一分一秒地過去。")]),_c('p',[_vm._v("突然，安琪對瓦林娜說道：「妳能使風傳訊對不對？」")]),_c('p',[_vm._v("瓦林娜道：「是啊，所以呢？」")]),_c('p',[_vm._v("安琪道：「請妳跟烏風說：不要殺女人了。」")]),_c('p',[_vm._v("瓦林娜尚未會意，安琪便往青銅門躍去。")]),_c('p',[_vm._v("鬼影守衛們想攔阻，但她早有預備，立刻舉起聖徽，喝令他們退下。")]),_c('p',[_vm._v("雙杖聖徽爆出剌眼的白光，逼退了守衛。")]),_c('p',[_vm._v("彼德、米庫斯和瓦林娜想要阻止，已來不及。說時遲，那時快，安琪一個箭步衝進了青銅門。")]),_c('p',[_vm._v("沒有人料到安琪會這麼做。進入大法師的轉生靈殿，無疑是送死。")]),_c('p',[_vm._v("安琪進門後，兩名鬼影守衛又舉劍擋住了門，不讓任何人通過。")]),_c('p',[_vm._v("青銅門內，安琪向他們說道：「每一個人都有所愛的人，每一個死亡都是傷心之事。沒有比較、沒有權衡、也沒有誰能犧牲別人的生命。我不相信非要喚醒大法師才能救世。如果執意如此，那麼身為醫者的信念，我寧願捨身成仁也不要殺害無辜。」")]),_c('p',[_vm._v("「不！」彼德大喊。")]),_c('p',[_vm._v("「永別了！希望你們成功喚醒大法師，到接天橋封聖，但是也要提防大法師可能帶有幽界的邪惡，因為這個轉生是如此的邪惡。」安琪平靜地說道，「未來造下殺業的時候，希望你們會想到今天，會想到曾經有一位同伴，是一名醫者。」")]),_c('p',[_vm._v("至少我忠於自己的仁心，安琪對自己說。在這死別之時，她也忍不住淚下。其實她是想活的。")]),_c('p',[_vm._v("「等一下，安琪！不要衝動！」米庫斯大喊。")]),_c('p',[_vm._v("但是安琪沒有回答。她的白袍隱沒在黑暗之中。鎖子甲碰撞的卡哩卡啦聲，也漸漸消失。")]),_c('p',[_vm._v("「不！」彼德衝向青銅門。鬼影守衛的長劍指住他，令他無法穿越。")]),_c('p',[_vm._v("米庫斯坐倒在地，一語不發，用手槌著石牆。")]),_c('p',[_vm._v("七柱燭台上的綠色火焰，仍不停搖擺。")]),_c('h3',[_vm._v("5.5.3. 歐奇力婭")]),_c('p',[_vm._v("洛南紀六十年三月十八日，愛森古墓")]),_c('p',[_vm._v("歐奇力婭睡了很久。但是，醒著的時間卻更長。")]),_c('p',[_vm._v("失去了身體，失去了眼身鼻舌，歐奇力婭的意識在孤獨中不停打轉。")]),_c('p',[_vm._v("有幾年，她為未來感到恐懼。如果計劃失敗，她將永遠封印在不壞的寶石當中，不朽，但也無處可去。")]),_c('p',[_vm._v("沒有日月的孤獨。")]),_c('p',[_vm._v("到後來，她完全忘記了時間，不知道一天、一月有什麼意義。")]),_c('p',[_vm._v("在空黑的意識裡，只有孤獨。")]),_c('p',[_vm._v("有幾年，歐奇力婭學會了靜心，讓漫長的等待成為一種觀照內在的過程。千年的內觀。")]),_c('p',[_vm._v("她的心變得很平靜。生前的衝動、傲氣和執念，在歲月的煎熬下，釀成了徹底的平靜、透明的智慧。")]),_c('p',[_vm._v("偶而她會想到自己的故事。")]),_c('p',[_vm._v("童年的歐奇力婭，生活很貧窮，從小就要幫媽媽顧攤賣點心、玩具。她的法藝在七歲時顯露。")]),_c('p',[_vm._v("當她發現自己能用手碰水就將它變成冰時，歐奇力婭十分高興。她也因此多了一項比較賺錢的本領：在夏季表演空手製冰。")]),_c('p',[_vm._v("十歲時，一場意外改變了歐奇力婭的命運。")]),_c('p',[_vm._v("那是一次監獄中有三名殺人犯越獄逃亡，其中一名還是流浪術士，能施法。當歐奇力婭聽到消息時，還在販賣她的冰品。她並不害怕。")]),_c('p',[_vm._v("當三名逃犯真的經過這條街時，歐奇利娜集中能量，施出寒冰錐，將三人一起冰封了。")]),_c('p',[_vm._v("愛森大法師之塔的人聽到消息，立刻派人來巡訪這名自學者。十歲的女孩，無師自通竟能施展高難度的寒冰錐，很快歐奇力婭就成為了法師學徒，學習讀寫、組合各式各樣的法材咒詞、還有算數與歷史。")]),_c('p',[_vm._v("在法藝大多由男性傳承的時代，歐奇利娜成為了首位女性的塑能大師。每個世紀總有一兩位女性擔任守護大師、結界大師或預言大師，但是司掌純粹力量的塑能大師不曾。這也讓她備受矚目。")]),_c('p',[_vm._v("在成為大法師之前的修煉中，歐奇力婭接觸了各式各樣的咒法和學派，也交了幾位好友。她缺少處世的圓滑，但是對純粹力量的掌握讓同儕不得不敬佩。")]),_c('p',[_vm._v("雖然接觸廣泛，歐奇力婭還是偏愛寒冰咒術。冰可以熄滅混亂之火，冰可以冷卻無名妄動，冰也可以溫暖人心。")]),_c('p',[_vm._v("施展轉生術後，在地底孤寂的長夜裡，歐奇力婭的心，漸漸像冰一般樣凝結，堅實地等待。")]),_c('p',[_vm._v("而冰溶霜化的一刻終於來到。")]),_c('p',[_vm._v("生氣，久違的生氣，讓歐奇力婭感到熟悉又陌生。")]),_c('p',[_vm._v("長年的沉寂，讓大法師對新的擾動十分敏感。不會錯，是人。")]),_c('p',[_vm._v("歐奇力婭集中精神，將靈魂勾上外面的驅體，然後把自己拉進去。")]),_c('p',[_vm._v("她重生了。")]),_c('p',[_vm._v("眼、耳、鼻、舌、身、意，六識重開。這過程並不舒服，過量的剌激讓歐奇力婭十分不適。")]),_c('p',[_vm._v("過了一陣子，頭痛漸漸減弱，也比較能看清四周時，歐奇力婭發現自己的左手很沉重。仔細一看，原來上面綁著一個邊緣銳利如刀的圓盾。")]),_c('p',[_vm._v("她解下圓盾，丟到地上。對法師而言，它太重了。")]),_c('p',[_vm._v("接著，歐奇力婭決定動一動，適應一下新的身體。")]),_c('p',[_vm._v("她一舉步，就發現自己竟然穿著一件笨重的甲胃。鎖子甲。")]),_c('p',[_vm._v("為什麼沒有先把屍體的甲胃脫掉再送進來呢？")]),_c('p',[_vm._v("她慢慢脫掉鎖子甲，又發現自己頸上竟佩戴著一串銀項鍊，上面鑲著銀質徽記，是兩根交叉的杖子。")]),_c('p',[_vm._v("歐奇力婭不認得這個徽記，於是把它解下，收進行囊。")]),_c('p',[_vm._v("行囊裡面有藥水、符咒、藥療用具、小錢袋、骷髏面具、小鏡子、還有一些布、小鍋、小鏟、叉子、小刀、火種、火石、乾糧、飲水、鹽巴以及許多不知名的藥草與香料。")]),_c('p',[_vm._v("只有牆壁上的螢光苔在微微泛著綠光，歐奇力婭希望亮一些。")]),_c('p',[_vm._v("火石…不對，她心道，我是法師啊。")]),_c('p',[_vm._v("「光噴古。」空中冒出了火把一樣的光亮。")]),_c('p',[_vm._v("歐奇力婭拿起鏡子，打量了一下自己的面孔。")]),_c('p',[_vm._v("太年輕了，可能還不到二十歲，古銅色的皮膚，眼睛和面部輪廓像是東部人。只有髮色是對的，黑褐色，可是太長了，也沒有綁成馬尾。")]),_c('p',[_vm._v("「錯形物換身始突風越。」")]),_c('p',[_vm._v("鏡中的人影，開始改變。膚色變白、頭髮變短、身高變得稍矮一些、肌肉也不那麼強壯。歲月的痕跡也出現了，現在的歐奇力婭回到四十多歲的模樣，生前的樣子，絲毫不差。")]),_c('p',[_vm._v("她又唸了幾個咒詞，召喚出整套的內外衣物，換上。這樣就和生前一樣了。只差大法師之袍與冰晶法杖。")]),_c('p',[_vm._v("她記得這兩件法器放在墓室的最右側的暗格裡。果然，沒幾下就找到了。由於法力的加持，大法師之袍和冰晶法杖竟然千年不壞，只是沾了灰塵。歐奇力婭用布將它擦拭乾淨。")]),_c('p',[_vm._v("久違了，老友，她心道，現在我們可以再次冒險了。為了不屬於我們的一個時代。")]),_c('p',[_vm._v("披上長袍，將扣環扣上。執起法杖，懷念的重量。")]),_c('p',[_vm._v("歐奇力婭思量片刻，決定不必自己喚醒其他的大法師。尋找神球比較重要。")]),_c('p',[_vm._v("她不喜多言，不想敘話，於是輕輕唸了一個咒，遁出了古墓，到達原野和藍天。")]),_c('p',[_vm._v("一個久違的世界。")]),_c('h3',[_vm._v("5.5.4. 傷逝")]),_c('p',[_vm._v("洛南紀六十年三月十八日，愛森古墓")]),_c('p',[_vm._v("「她一直沒出來。」彼德痛苦地說道，「已經過很久了。」")]),_c('p',[_vm._v("「很難想像我爸會有什麼反應，還有集漁鎮的修女索菲亞…」米庫斯嗚咽著，「他們把她托付給我，我卻把她帶到死亡。天哪。」")]),_c('p',[_vm._v("瓦林娜不知如何是好。這場悲劇可說是烏風的獨行造成的，而烏風是她的同伴。")]),_c('p',[_vm._v("她感到悲傷、不知所措、還有自責。")]),_c('p',[_vm._v("不知道烏風回來會怎樣？風訊已讓他知道安琪的犧牲，他可能會調整好情緒才進來吧？")]),_c('p',[_vm._v("或許他進來的時候，就像平常一樣冰冷。但那樣更糟。如果激怒了彼德跟米庫斯，他們或許會攻擊他。")]),_c('p',[_vm._v("是了，瓦林娜心道，我可以用咒詞來撫平他們心中的痛苦和悲傷。")]),_c('p',[_vm._v("但是她很快打消了唸頭。這是不敬，對安琪的不敬。")]),_c('p',[_vm._v("三個人的思緒都很紛亂，心情跌到谷底。彼德尤其悲傷，因為他錯過了一個救她的機會。")]),_c('p',[_vm._v("如果還能再見到安琪，彼德心道，我一定要告訴她我有多愛她。")]),_c('p',[_vm._v("如果沒有大戰、如果沒有使命、如果沒有大法師，就我們兩個人，在隱者庭苑過幸福的日子，不是很好嗎？為什麼人要冒險呢？為什麼結局會變成這樣呢？")]),_c('p',[_vm._v("為什麼她可以完成她的堅持，然後讓我活著受苦呢？")]),_c('p',[_vm._v("不公平，彼德心想，太不公平。如果角色對調，可能會比較好。但是太遲了，一切都太遲了。")]),_c('p',[_vm._v("「故事還是要繼續下去，」彼德喃喃自語，「不管有意義還是沒有意義，我們都要喚醒大法師。去他的古墓！來，喝酒！」")]),_c('p',[_vm._v("他從行囊中拿出家鄉的葡萄酒，放到地上。接著坐倒在地，看著綠色的慘光映著酒瓶。")]),_c('p',[_vm._v("「來，喝酒！」彼德拔下瓶塞，灌了一大口，「米庫斯，來！喝到爛醉，醒來就會發現一切都是夢！」說著他又哭了起來。")]),_c('p',[_vm._v("彼德又灌了一大口，卻一口氣岔到，嗆得把酒噴在地上，猛咳不止。")]),_c('p',[_vm._v("「彼德，別丟人現眼的！」米庫斯一面敲著石壁，一面說道，「你就不能安靜一點嗎？」")]),_c('p',[_vm._v("彼德一面喘氣，一面說道：「安靜是可以啦！那我一個人去散一散，不打擾你。失陪了。」說著他走出房間，進入通道。")]),_c('p',[_vm._v("房間裡，沒有人說話，只有米庫斯急促的呼吸聲。")]),_c('p',[_vm._v("時間一分一秒地過去。通道那邊不時傳出幾聲狂吼。")]),_c('p',[_vm._v("突然，有沉重的腳步聲接近，瓦林娜和米庫斯往通道方向望去。沒有人影。")]),_c('p',[_vm._v("腳步聲進入了房間。突然地上憑空出現一具屍體。")]),_c('p',[_vm._v("「烏風！」瓦林娜道。")]),_c('p',[_vm._v("但是烏風沒有回答。在隱匿之球的保謢下，他悄悄地離開了。")]),_c('p',[_vm._v("瓦林娜著手調製藥劑。她從行囊掏出一個玻璃瓶，倒入八分滿的古墓黑藥，然後用小刀劃破左手無名指，滴了幾滴鮮血。")]),_c('p',[_vm._v("她舉著瓶子搖晃，直到血液與完藥劑完全混合。")]),_c('p',[_vm._v("然後是注入屍體的口中。")]),_c('p',[_vm._v("瓦林娜感到十分困擾：為什麼是我？為什麼是我？每個人都很沮喪、很悲傷，不是嗎？為什麼現在是我一個人去調藥灌屍體？")]),_c('p',[_vm._v("當藥劑注入的同時，瓦林娜注視著屍體的臉孔。大約二十歲、皮膚曬得很黑、方臉、黑色捲髮、鬍子很久沒刮、像團亂草。")]),_c('p',[_vm._v("「每一個人都有所愛的人，每一個死亡都是傷心之事。沒有比較、沒有權衡、也沒有誰能犧牲別人的生命。」安琪曾經這麼說。")]),_c('p',[_vm._v("沒有比較、沒有權衡。")]),_c('p',[_vm._v("但是當天降星使、大難來臨，有能力與之抗衡的人，就比較重要。至少對旁人來說是這樣。")]),_c('p',[_vm._v("這是為什麼要喚醒大法師，即使犧牲無辜也再所不惜。即使行惡，但是換得更大的善。這就是權衡。")]),_c('p',[_vm._v("但是對被犧牲的人來講，那就是一切；對愛他的人來講，那就是一切。")]),_c('p',[_vm._v("瓦林娜漸漸明白安琪的意思。")]),_c('p',[_vm._v("藥，一點一點地注入。直到瓶子倒乾。")]),_c('p',[_vm._v("瓦林娜拖著屍體，走向青銅門。米庫斯一言不發，起身抬著屍體的另一端，一起走向轉生之殿。")]),_c('p',[_vm._v("門裡，還是一片黑暗。")]),_c('div',{staticClass:"ui buttons"},[_c('router-link',{staticClass:"ui orange huge button",attrs:{"to":"/e4"}},[_vm._v("5.4節")]),_c('router-link',{staticClass:"ui green huge button",attrs:{"to":"/e6"}},[_vm._v("5.6節")])],1)])])
