你失去了永生？」瓦林娜問道。")]),_c('p',[_vm._v("「因為這次的災難，我不能坐視人類被毀滅，」鐘墨芝石道，「如果不是千年前我鑄下大錯，人類根本不需經歷漫長的黑暗時代，現在可能有更高的文明、更好的秩序了。」")]),_c('p',[_vm._v("他的聲音透著深切的悔恨。")]),_c('p',[_vm._v("「失去了日與月的力量之後，我又放棄了永恆的生命與無盡的法力，就只有一個目標，」鐘墨芝石嚴肅地說道，「讓人類成長，有能力挑戰不朽的星使、渡過大災難、將文明重建。」")]),_c('p',[_vm._v("一陣漫長的沉默。")]),_c('p',[_vm._v("「我們不需要重建，」烏風道，「愛森的遺產很重要，但是現代的人，有現代人的路要走。」")]),_c('p',[_vm._v("「我也認為，」瓦林娜道，「『過去』因為『現在』而有意義，『現在』並不是為了『過去』而存在。精靈王，或許你可以不必太執著於悔恨。」")]),_c('p',[_vm._v("鐘墨芝石陷入更深的沉默。")]),_c('h3',[_vm._v("4.4.3. 封聖這條路")]),_c('p',[_vm._v("「如果愛森聖王沒有再現，那麼只剩下一條路了。」占卜師緩緩地說道，「這條路，愛森的大法師走過，但是失敗了。你們有自信能通過它嗎？」")]),_c('p',[_vm._v("「什麼路？」烏風問。")]),_c('p',[_vm._v("「通過極北之地的接天橋，到星界封聖。」鐘墨芝石說道，「封聖和封神不同，但對凡軀而言，都是艱難無比的考驗。」")]),_c('p',[_vm._v("「我沒聽師父講過…」瓦林娜沉吟道，「那是個怎樣的地方？」")]),_c('p',[_vm._v("「接天橋是從凡界通往星界的道路，從星界可以再通到天界。」占卜師道，「接天橋本來並不危險，但在封神的年代，太多的精靈上達天界，使星辰的光芒蓋過日月。」")]),_c('p',[_vm._v("「後來呢？」")]),_c('p',[_vm._v("「傳說『莫可名者』將星辰敲落凡間，化為寶石。每顆寶石都曾是封神的精靈，所以它們都有不同的色彩、不同的故事。也是因此，寶石才含有法力，成為法師和煉金術師兢相爭奪的寶物。」")]),_c('p',[_vm._v("「誰是『莫可名者』？」")]),_c('p',[_vm._v("「『莫可名者』不是神，」占卜師道，「沒有人瞭解它，也不知道它是唯一還是無限。有智慧的精靈也不知道，『莫可名者』在萬物之前存在，或者它即是萬物的大我。」")]),_c('p',[_vm._v("「聽起來很玄。」")]),_c('p',[_vm._v("「精靈和人類不一樣，」鐘墨芝石道，「我們沒有『信仰』，只有『感應』。在永動的輪、不息的風、陽焰月影和萬物的呼吸之中，我們感應到一股調和、一股秩序。有時它會變化、難以掌握，但是我們可以隨著它呼吸。我們稱它為『莫可名者』。」")]),_c('p',[_vm._v("「為什麼你們沒有信仰？」瓦林娜問道。")]),_c('p',[_vm._v("「精靈很少憶惻未知的事，更不會把這種憶測化為儀式、戒律和教條。」鐘墨芝石道，「我們是永恆的存在，不必害怕衰老和死亡。而且，早在天界和幽界有神祇之前，我們就在大地上歡唱、舞蹈了。現在天幽兩界的諸神，都曾是大地上歌舞的精靈。」")]),_c('p',[_vm._v("「幽界的冥王呢？」烏風突然插道。")]),_c('p',[_vm._v("「唉，你的夢還在困擾著你吧？」鐘墨芝石嘆道，「冥王是古精靈，在封神的年代後，她收集了許多寶石，引動精靈心中的貪婪，將他們帶到地底，變成了闇精靈。」")]),_c('p',[_vm._v("「因為另一條封神的路在地底，通往幽界。」鐘墨芝石道，「雖然上不了接天橋，能闖過幽域，到達亡者之地，一樣可以封神。唉，明明已經長生不死，還想追求無盡的權能，也難怪會觸怒『莫可名者』。")]),_c('p',[_vm._v("「要小心，冥王是一個支配者，充滿權謀與心機。地底的闇精靈都會施法，而且具有抵抗法術的能力。最危險的是，騙詐、幻術和心靈支配，對他們而言根本是家常便飯。」")]),_c('p',[_vm._v("身為一名結界師，瓦林娜很清楚心靈支配有多麼恐怖。它不是魅惑，也不是暗示，而是直接鉗住對方的意志，迫使對方服從。被支配的人，甚至會攻擊自己的同伴。")]),_c('p',[_vm._v("瓦林娜的法術書上有『支配術』的咒文，但是她還不能掌握。就她所知，只有風谷三師--乘風者阿克紐、逐雲者菲妮安、舞空者耐文有能力施展這困難的咒語。")]),_c('p',[_vm._v("她知道黑暗的聲音不斷在夢中呼喚烏風。她不想失去他，不希望他被支配、變成敵人。")]),_c('p',[_vm._v("「我也是結界師，」瓦林娜堅定地說道，「我會保護他的。」")]),_c('p',[_vm._v("「我們會小心。」烏風冷冷地說道。")]),_c('p',[_vm._v("「那就好，願你們的旅程順利。」鐘墨芝石道，「封聖並不容易。」")]),_c('p',[_vm._v("「什麼是封聖？」瓦林娜問道。")]),_c('p',[_vm._v("「封聖是在平凡中找到不凡，在有限的生命中創造永恆，」鐘墨芝石道，「『莫可名者』阻斷了封神，讓接天橋變得危險重重，卻也開了一條新的道路。封神是追求不朽和權能，封聖則是找回初心，從『莫可名者』處，找回根本的勇氣、希望、力量、覺察和真心。")]),_c('p',[_vm._v("「這條路不能一個人走，也不能兩個人去。要開啟接天橋，必須要具有這五種美德的人同行。」")]),_c('p',[_vm._v("「聽起來很奇怪，」瓦林娜道，「我們認識一個叫芬斯特藍的探險家，他很優秀。但是另外的人要到哪裡找呢？一定要五個人嗎？」")]),_c('p',[_vm._v("「一定。」占卜師道，「我曾寫過相關的歷史，現在我持有的真知之球也這麼顯示。我不知道為什麼，但是『莫可名者』必有其道理。」")]),_c('p',[_vm._v("「我是風谷的人，心中的希望就像不息的風，」瓦林娜道，「烏風有力量。」")]),_c('p',[_vm._v("「瓦林娜，不是的，」占卜師道，「烏風在尋找力量，而他的美德是勇氣，從深淵中站起的勇氣。」")]),_c('p',[_vm._v("烏風感到心頭一震。")]),_c('p',[_vm._v("「另外三個人呢？」瓦林娜問。")]),_c('p',[_vm._v("「你們即日西行，我會派守護靈在暗中引路開道，幫你們渡過險阻，帶你們到愛森古墓。那裡有你們要找的同伴，以及大法師的遺產。」")]),_c('p',[_vm._v("「然後呢？我們就去接天橋嗎？」")]),_c('p',[_vm._v("「或許是，或許不是。」占卜師道，「你們進入古墓之後，路就會自己展開。現在我的眼睛，也還看不清楚。我只看到，你們在古墓會遇到很大的危險，還會面對一次很大的爭執。可能有人會送命。」")]),_c('div',{staticClass:"ui buttons"},[_c('router-link',{staticClass:"ui orange huge button",attrs:{"to":"/d3"}},[_vm._v("4.3節")]),_c('router-link',{staticClass:"ui green huge button",attrs:{"to":"/d5"}},[_vm._v("4.5節")])],1)])])
