'p',[_vm._v("貴族多半是早年隨先皇打天下的將軍、騎士、參謀的後裔，也有靠金錢買來爵位的商人。這類商人的領地通常在帝國外圍，含有可以致富的產物。比方說冰刃山脈的銀礦、西南山脈的銅礦和錫礦、南衛山脈的金礦和鐵礦等等。")]),_c('p',[_vm._v("商人們的領地往往只包含礦坑和外圍一點點的土地，這對他們已完全足夠。朝庭的人也樂於見到有人能幫他們開礦，讓他們課稅。")]),_c('p',[_vm._v("至於將領們的子孫，則是極度地參差不齊。")]),_c('p',[_vm._v("有的人保持了父輩崇尚武德的傳統，成為才德兼備、文武雙全的騎士；也有的人因為沒有經歷過戰亂而過著奢靡、沉淪的生活；有的向商人學習致富術，並把封邑當成財產來經營賺錢；也有的如基斯坦卡候爵般，以無道的暴政作威作福，敗壞了父輩的一世英名，辱沒了曾經光榮的姓氏。")]),_c('p',[_vm._v("統治南北沃土的諾魯夫王，也就是沃土公爵，是皇帝陸下的親弟弟，正值壯年，是深得民心的領主。他與北地領主米爾王，被稱為帝國末日時期的中流砥柱。")]),_c('p',[_vm._v("在南沃土被盜賊佔據的日子裡，諾魯夫本來可以將他們掃平。但他認為罪不在平民，於是更加用心整治北沃土，讓盜賊們有回歸田畝的機會。只可惜他的德政，也敵不過連年的天災。")]),_c('p',[_vm._v("貴族如此，平民的日子就悲慘得多了。在這動盪的年代中，他們不是被蝗害和疫病擊倒，就是死於苛政和刑具之下。再也沒有哪塊土地是永遠安全的，也沒有哪條命是能得享天年的。")]),_c('p',[_vm._v("農民一離開土地，就像落葉脫離樹木一般，隨風飄盪，什麼事都能幹，什麼事都願意去幹。有的加入教會，只為了混一口飯吃；有的加入探險的行列，去西方作生與死的豪賭；有的揭竿起義，拚著身首分離，也要活下去。")]),_c('p',[_vm._v("在人命最賤的時代，沒有人還能分得了是非對錯。為了生存，無論幹下多麼傷天害理的惡事，也能被諒解。他們連悔罪的時間都沒有，又得為下一餐的著落打算。")]),_c('p',[_vm._v("因此，任何一個教會都能籍機擴展勢力。善神的修士幫人治病療傷；邪神的信徒則以死後的業報讓人懼怕；宿命之神的修士教人等待的藝術，因為苦難的歲月並非恆常。")]),_c('p',[_vm._v("（第一章完）")]),_c('h1',[_vm._v("側記")]),_c('p',[_vm._v("故事的原味，總比側記好看，不過側記並非為了好看而寫。而是作者本身試圖用更直白的方式，來說自己的故事。")]),_c('p',[_vm._v("一開始寫《諸星之戰》時，我還是中學生。")]),_c('p',[_vm._v("當時在迷紙上角色扮演遊戲(Tabletop Role-Playing Game, TRPG)。有一整學期，每週六和同學在家裡跑團，而諸星之戰的設定，像諾薩里亞、風谷、愛森、也就在遊戲中邊跑邊成形。")]),_c('p',[_vm._v("原本寫諸星之戰時，想要拿跑團的故事當主軸，來延伸。但是，一開始隕星之夜的芬斯特藍，就不是遊戲中的人物，我想也是一個預兆。告訴我小說有她的生命，不是跑團記那麼單純。")]),_c('p',[_vm._v("故事的發展和當時的遊戲，差異很大。角色的性格也頗有不同。不過，雖然有一些新的法術和彈性的戰鬥場景，多少還是看得出一些龍與地下城(AD&D)設定的斧鑿痕跡，我想這是我的小說功力還不夠吧。")]),_c('p',[_vm._v("另外，從中學開始寫的好處是，隨著我自己的年紀增長，故事中人物的性格也會愈加鮮明而成熟。在接下來幾章會看得出來。")]),_c('p',[_vm._v("至於在奇幻小說流行的時代，為什麼還要多寫一部呢？")]),_c('p',[_vm._v("其實，有吳承恩的《西遊記》、托爾金的《魔戒》、麥克安迪的《說不完的故事》、娥蘇拉魏恩的《地海巫師》，真的不需要多一部奇幻小說了。")]),_c('p',[_vm._v("只是，我有幾個問題，想要在諸星之戰中，和讀者一起思考。")]),_c('p',[_vm._v("其中之一是：怪物為什麼該死？")]),_c('p',[_vm._v("「怪物為什麼該死」困擾了我很久。電腦遊戲和一些小說中，怪物該死是不需要理由的。但是，現實世界中，哪有什麼人形生物，該死是不需要理由的呢？")]),_c('p',[_vm._v("怪物是什麼？怪物不就是那些和我們無法溝通的人形生物嗎？就像西班牙人遇到美洲人，英國人遇到非州人，中國人遇到南島人那樣。")]),_c('p',[_vm._v("當視他為「物」，就比視他為「怪人」更糟了。")]),_c('p',[_vm._v("諸星之戰的第一章，